html::-webkit-scrollbar {
    width: 0px;
}

* {
    box-sizing: border-box;
    margin: 0px;
    padding: 0px;
}

a {
    color: inherit !important;
    text-decoration: none !important;
}

a:hover {
    color: goldenrod !important;
}

.cursor-pointer {
    cursor: pointer;
}

.footer {
    width: 100%;
    height: 65px;
    background: #24242c;
}

.header {
    position: relative;
    width: 100%;
    height: auto;
    background: #24242c;
}

.header .title {
    display: flex;
    flex-direction: column;
    height: 50px;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    letter-spacing: 1px;
}

.header .title__name {
    width: 100%;
    margin: 0px;
    font-size: 24px;
    font-family: 'Open Sans';
    color: whitesmoke;
}

.header .title__name .with-TMDB-Api {
    font-size: 14px;
}

.title__commonwealth {
    color: whitesmoke;
    margin-bottom: 0px;
    width: 100%;
    font-size: 14px;
    padding-left: 5px;
}

.header .title__sub-name {
    padding-left: 1px;
    color: goldenrod;
}

.login {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.login-form__title {
    font-size: 22px;
    text-align: center;
    margin-bottom: 10px;
}

.main {
    padding: 0px !important;
    margin-top: 40px;
    margin-bottom: 40px;
}

.filters {
    padding-left: 0px !important;
    padding-right: 25px !important;
}

.movies {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 0px 0px 0px 10px !important;
}

.movie-card {
    width: 200px;
    max-width: 200px;
    height: auto;
    max-height: 375px;
    position: relative;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    margin-bottom: 20px;
    border-radius: 0px 0px 5px 5px;
    background: #ffffff;
}

.movie-card__img {
    position: relative;
    width: 200px;
    height: 300px;
    object-fit: cover;
    border-radius: 5px 5px 0px 0px;
}

.movie-card__desc {
    position: relative;
    width: 100%;
    height: 75px;
    padding: 0px 10px;
}

.movie-card__desc__title {
    position: relative;
    width: 100%;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    font-weight: bold;
    margin-top: 15px;
    margin-bottom: 0px;
}

.movie-card__desc__release-date {
    position: relative;
    width: 100%;
    font-size: 15px;
}

.filters-label {
    font-size: 22px;
    font-weight: bold;
    margin-bottom: 13px;
}

.sort {
    padding: 15px;
    border: 1px solid whitesmoke;
    border-radius: 10px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, .2);
    background: #ffffff;
}

.pagination {
    margin: 0px !important;
    padding: 0px !important;
}

.total-pages {
    padding: 0px !important;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #e2e6ea;
}

.primary-release-year {
    display: block;
    margin: 0px 0px 4px;
    font-size: 16px;
    font-weight: 500;
}

.genre-label {
    display: block;
    margin: 10px 0px 4px;
    font-size: 16px;
    font-weight: 500;
}

.genres {
    margin-top: 7px;
    padding-left: 5px;
}

.genres__item__label {
    position: relative;
    top: -1.5px;
    margin-left: 6px;
    font-size: 15px;
    font-weight: 500;
    margin-bottom: 2px;
}

.invalid-feedback {
    display: block !important;
}

input.invalid {
    border-color: #dc3545;
}

.dropdown {
    display: flex;
    justify-content: flex-end;
}

.user-img {
    width: 40px;
    height: 40px;
    cursor: pointer;
    object-fit: cover;
}

.dropdown__wrapper {
    position: relative;
}

.dropdown__popover {
    position: absolute;
    z-index: 999;
}

.dropdown__wrapper-content {
    position: relative;
    z-index: 999;
}

.dropdown__cover {
    position: fixed;
    top: 0px;
    right: 0px;
    bottom: 0px;
    left: 0px;
    z-index: 777;
}

.dropdown__menu {
    position: relative;
    width: 180px;
    background: #fff;
    box-shadow: 0 1px 2px 0 rgba(64, 80, 108, 0.2),
        0 0 24px -1px rgba(64, 80, 108, 0.2);
    border-radius: 8px;
}

.dropdown__menu::after {
    content: "";
    position: absolute;
    right: 10px;
    top: -14px;
    border: 7px solid transparent;
    border-bottom: 7px solid #fff;
}

.dropdown__menu-item {
    padding: 16px;
    font-size: 14px;
    color: #657389;
    line-height: 1;
    cursor: pointer;
    border-radius: 10px 10px 0px 0px;
}

.dropdown__menu-item:first-of-type {
    border-radius: 10px 10px 0px 0px !important;
}

.dropdown__menu-item:last-of-type {
    border-radius: 0px 0px 10px 10px;
}

.dropdown__menu-item:not(:last-child) {
    border-bottom: 1px solid rgba(64, 80, 108, 0.1);
}

.dropdown__menu-item:hover {
    background-color: rgba(245, 246, 248, 0.5);
    color: #40506c;
}

.movie-settings {
    position: absolute;
    display: flex;
    width: 100%;
    justify-content: flex-end;
    padding: 7px 7px 0px 7px;
    color: black;
}

.movie-settings .more-horiz {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 14px;
    width: 22px;
    height: 22px;
    border-radius: 100%;
    background: rgb(255 255 255 / 50%);
    cursor: pointer;
    transition: all 0.2s;
}

.movie-settings .more-horiz .more-horiz__icon {
    font-size: 1.2rem;
}

.movie-settings .more-horiz:hover {
    background: rgb(255 255 255 / 70%);
}

.settings-menu {
    position: relative;
    width: 200px;
    background: #fff;
    box-shadow: 0 1px 2px 0 rgba(64, 80, 108, 0.2),
        0 0 24px -1px rgba(64, 80, 108, 0.2);
    border-radius: 8px;
    z-index: 999;
}

.settings-menu::after {
    content: "";
    position: absolute;
    right: 24px;
    top: -14px;
    border: 7px solid transparent;
    border-bottom: 7px solid #fff;
}

.settings-menu .settings-menuItem {
    display: flex;
    align-items: center;
    padding: 12px;
    font-size: 14px;
    color: #657389;
    line-height: 1;
    cursor: pointer;
    border-radius: 10px 10px 0px 0px;
}

.settings-menu .settings-menuItem .MuiSvgIcon-root {
    font-size: 1.3rem;
}

.settings-menu .settings-menuItem .settings-title {
    margin-left: 7px;
    font-size: 14px;
}

.settings-menu .settings-menuItem:hover {
    background-color: rgba(245, 246, 248, 0.5);
    color: #40506c;
    border-radius: 10px;
}

.movie-settings__wrapper {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}

.movie-settings__wrapper .more-horiz {
    margin-bottom: 11px;
    margin-right: 4px;
    margin-top: 2px;
}

.movie-settings__wrapper .settings-menu::after {
    right: 10px;
}

.movie-card__desc__vote-average {
    position: absolute;
    margin: 0px;
    width: 35px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 35px;
    top: -22px;
    left: 17px;
    color: white;
    box-sizing: border-box;
    padding: 0px;
    border-radius: 100%;
    background: #24242c;
    font-size: 15px;
}

.dropdown__menu .user-name {
    font-size: 16px;
    color: black;
    font-weight: bold;
}

.dropdown__menu .user-name .user-name__sub-title {
    margin-top: 10px;
    margin-bottom: 0px;
    font-size: 13px;
    color: #657389;
    font-weight: normal;
}

.user-page {
    width: 100%;
    height: auto;
}

.user-page__header {
    position: relative;
    width: 100%;
    height: 280px;
}

.user-page__header .image {
    width: 100%;
    height: 280px;
    object-fit: cover;
}

.user-page__header .inner-content {
    position: absolute;
    background: rgba(0, 0, 0, 0.7);
    width: 100%;
    height: 100%;
    top: 0px;
}

.user-page__header .inner-content .content {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    padding-left: 100px;
}

.user-page__header .inner-content .content .user-img {
    width: 150px;
    height: 150px;
    object-fit: cover;
    border-radius: 100%;
}

.user-page__header .inner-content .content .user-data {
    width: 200px;
    height: 150px;
    padding: 13px 40px;
    color: white;
}

.user-page__header .inner-content .content .user-data .user-data__nickname {
    padding-left: 3px;
    font-size: 14px;
}

.favourite-movies-tab .favourite-movies .favourite-movies__item {
    display: flex;
    margin: 20px 0px;
    width: 100%;
    height: 230px;
    border-radius: 4px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
    border: 1px solid rgba(var(--lightGrey), 1);
    background-color: #fff;
}

.favourite-movies-tab .favourite-movies .favourite-movies__item .poster-img__image {
    height: 230px;
}

.favourite-movies-tab .favourite-movies .favourite-movies__item .description {
    width: 100%;
}

.favourite-movies-tab .favourite-movies .favourite-movies__item .description__top-block {
    width: 100%;
    height: 60px;
    display: flex;
    padding: 5px 20px;
}

.favourite-movies-tab .favourite-movies .favourite-movies__item .description__top-block .rate {
    margin: 0px;
    margin-top: 6px;
    width: 40px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    box-sizing: border-box;
    padding: 0px;
    border-radius: 100%;
    background: #24242c;
    font-size: 15px;
    margin-right: 14px;
}

.favourite-movies-tab .favourite-movies .favourite-movies__item .description__top-block .title .movie-name {
    font-size: 1.2em;
    margin-bottom: 0;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
    text-overflow: ellipsis;
    overflow: hidden;
    font-weight: 700;
}

.favourite-movies-tab .favourite-movies .favourite-movies__item .description__top-block .title .release-date {
    white-space: nowrap;
    color: #999;
    font-size: 15px;
    margin: 0px;
}

.favourite-movies-tab .favourite-movies .favourite-movies__item .description__middle-block {
    width: 100%;
    padding: 0px 20px;
}

.favourite-movies-tab .favourite-movies .favourite-movies__item .description__middle-block .movie-overview {
    width: 100%;
    height: 110px;
}

.favourite-movies-tab .favourite-movies .favourite-movies__item .description__middle-block .movie-overview .text {
    display: -webkit-box;
    -webkit-line-clamp: 4;
    -webkit-box-orient: vertical;
    text-overflow: ellipsis;
    overflow: hidden;
    margin-bottom: 0px;
    padding-top: 10px;
}

.favourite-movies-tab .favourite-movies .favourite-movies__item .description__bottom-block {
    display: flex;
    width: 100%;
    height: 60px;
    padding: 0px 20px;
}

.favourite-movies-tab .favourite-movies .favourite-movies__item .description__bottom-block .favourite,
.watchlist {
    width: 150px;
    height: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.favourite-movies-tab .favourite-movies .favourite-movies__item .description__bottom-block .favourite:hover,
.watchlist:hover {
    background: whitesmoke;
}

.favourite-movies-tab .favourite-movies .favourite-movies__item .description__bottom-block .icon {
    width: 30px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid;
}

.movie-header {
    position: relative;
    width: 100%;
    height: 512px;
}

.movie-header .movie-header__custom-bg {
    position: relative;
    background-image: linear-gradient(to right, #24242c 20%, rgba(18.82%, 25.49%, 30.59%, 0.64) 100%);
}

.movie-header .movie-header__bg-img {
    position: relative;
    width: 80%;
    z-index: -2;
}

.movie-header .movie-header__single-column {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0px;
}

.movie-header .movie-header__description {
    width: 100%;
    color: white;
}

.movie-header .movie-header__title {
    color: whitesmoke;
}

.movie-header .movie-header__release {
    color: white;
    font-size: 14px;
}

.movie-header .movie-header__tagline {
    font-size: 1.1em;
    font-weight: 400;
    font-style: italic;
    opacity: 0.7;
}

.movie-header .movie-header__rate,
.movie-header__favourite,
.movie-header__watchlist {
    width: 47px;
    height: 45px;
    background: rgb(36 36 44 / 0.7);
    font-size: 16px;
    box-shadow: 1px 1px 10px #24242c;
}